import { CloudApiGateway } from './gateway';

type MemberRole = 'editor' | 'debugger';

export type MemberDTO = {
  id: string;
  authorId: string;
  name: string;
  email: string;
  role: MemberRole;
};

export class MemberAPI {
  async create(values: {
    organizationId: string;
    email: string;
    teams: string[] | null;
  }): Promise<MemberDTO> {
    return CloudApiGateway.post(`organizations/${values.organizationId}/members`, {
      email: values.email,
      teams: values.teams,
    });
  }

  async delete(values: { organizationId: string; authorId: string }): Promise<void> {
    return CloudApiGateway.delete(
      `organizations/${values.organizationId}/members/${values.authorId}`,
    );
  }

  async list(organizationId: string): Promise<MemberDTO[]> {
    return CloudApiGateway.get(`organizations/${organizationId}/members`);
  }

  async get(organizationId: string, authorId: string): Promise<MemberDTO> {
    return CloudApiGateway.get(`organizations/${organizationId}/members/${authorId}`);
  }

  async update(
    organizationId: string,
    authorId: string,
    values: Partial<MemberDTO>,
  ): Promise<MemberDTO> {
    return CloudApiGateway.patch(`organizations/${organizationId}/members/${authorId}`, values);
  }
}
